'use client'

import { useEffect, useState } from 'react'
import supabase from '@/lib/supabaseBrowserClient'
import { useRouter } from 'next/navigation'
import { motion } from 'framer-motion'

export default function PostPage() {
  const [jobs, setJobs] = useState<any[]>([])
  const [posterEmail, setPosterEmail] = useState('')
  const router = useRouter()

  useEffect(() => {
    const fetchData = async () => {
      const { data: userData } = await supabase.auth.getUser()
      const email = userData.user?.email
      if (!email) return router.replace('/login')

      setPosterEmail(email)

      const { data, error } = await supabase
        .from('jobs')
        .select('*')
        .eq('poster_email', email)
        .order('created_at', { ascending: false })

      if (!error) setJobs(data || [])
    }

    fetchData()
  }, [router])

  return (
    <>
      {/* Static background layer ‚Äî apricot */}
      <div className="fixed inset-0 z-0" style={{ background: 'rgb(241,180,151)' }} />
  
      <motion.div
        initial={{ x: '-100%' }}
        animate={{ x: 0 }}
        exit={{ x: '100%' }}
        transition={{ duration: 0.6 }}
        style={{
          position: 'absolute',
          width: '100vw',
          height: '100vh',
          overflow: 'auto',
          background: 'rgb(152,183,191)'
        }}
        className="z-10"
      >
        <div className="relative px-6 max-w-3xl mx-auto pt-6">
          <h1 className="text-2xl font-bold mb-6 text-center">My Posted Jobs</h1>
  
          <div className="space-y-6">
            {jobs.map((job) => (
              <div key={job.id} className="border p-4 rounded bg-white">
                <h3 className="text-lg font-semibold">{job.title}</h3>
                <p className="text-gray-600">{job.description}</p>
                <p className="text-sm text-gray-400 mt-1">
                  {job.is_paid ? 'üí∞ Paid' : 'üö´ Unpaid'}
                </p>
                <p className="text-xs text-gray-500 mt-2">
                  Claimer: {job.selected_claimer_email?.split('@')[0] || 'None yet'}
                </p>
              </div>
            ))}
          </div>
  
          <div
            onClick={() => router.push('/find')}
            className="fixed top-0 right-0 h-screen w-3 bg-[rgb(241,180,151)] hover:w-32 transition-all duration-300 flex items-center justify-center cursor-pointer z-50 group">
            <span className="text-sm text-black opacity-0 group-hover:opacity-100 transition duration-300 rotate-90 whitespace-nowrap">
              ‚Üê Find
            </span>
          </div>
        </div>
      </motion.div>
    </>
  )
}